import { motion } from "framer-motion";

const TitleSection = ({ open }) => {
  return (
    <div className="mb-3 border-b border-slate-300 pt-1 pb-3">
      <div className="flex items-center space-x-2 px-1 rounded-md">
        <motion.div
          layout
          className="grid size-8 place-content-center rounded-md"
        >
          <Link href={"/"}>
            <Logo className="w-8" />
          </Link>
        </motion.div>
        {open && (
          <motion.div
            layout
            initial={{ opacity: 0, y: 12 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.125 }}
          >
            <span className="block text-xs text-slate-800 font-bold">
              3aB0z
            </span>
            <span className="block text-xs text-slate-500">Admin Plan</span>
          </motion.div>
        )}
      </div>
    </div>
  );
};

export default TitleSection;
